<template>
  <v-layout column id="dashboard" pa-2>
    <v-flex d-flex align-center class="text-xs-center">
      <h1>{{ pageTitle }}</h1>
    </v-flex>
    <v-flex d-flex class="text-xs-center"
        v-for="user in users"
        :key="user.id">
        <span>{{ user.name }}</span>
      </v-flex>
      <v-flex d-flex class="text-xs-center">
        <span>{{ name }}</span>
      </v-flex>
    <v-layout justify-center>
    <v-form ref="form" v-model="valid" lazy-validation>
    <v-text-field
      v-model="name"
      :rules="nameRules"
      :counter="10"
      label="Name"
      required
    ></v-text-field>
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field>
    <v-text-field
      v-model="password"
      :rules="passwordRules"
      label="Password"
      required
    ></v-text-field>
    <!--v-select
      v-model="select"
      :items="items"
      :rules="[v => !!v || 'Item is required']"
      label="Item"
      required
    ></v-select>
    <v-checkbox
      v-model="checkbox"
      :rules="[v => !!v || 'You must agree to continue!']"
      label="Do you agree?"
      required
    ></v-checkbox-->

    <v-btn
      :disabled="!valid"
      @click="registerNewUser"
    >
      submit
    </v-btn>
    <v-btn @click="clear">clear</v-btn>
  </v-form>
    </v-layout>
    <v-layout justify-center>
      <v-btn outline round
        class="dashBtn"
        color="purple"
        @click="getUsersList"
        >Show users</v-btn>
      <v-btn outline round
        class="dashBtn"
        color="purple">Remove user</v-btn>
    </v-layout>
  </v-layout>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase/app'
import 'firebase/auth'

export default {
  name: 'dashboard',
  data: () => ({
    users: [],
    pageTitle: 'Dashboard',
    valid: true,
    name: '',
    nameRules: [
      v => !!v || 'Name is required',
      v => (v && v.length <= 10) || 'Name must be less than 10 characters'
    ],
    email: '',
    emailRules: [
      v => !!v || 'E-mail is required',
      v => /.+@.+/.test(v) || 'E-mail must be valid'
    ],
    password: '',
    passwordRules: [
      v => !!v || 'Password is required',
      v => (v && v.length <= 12) || 'Password must be less than 12 characters'
    ],
    select: null,
    items: [
      'Item 1',
      'Item 2',
      'Item 3',
      'Item 4'
    ],
    checkbox: false
  }),
  methods: {
    getUsersList () {
      db.collection('users').get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          console.log(doc.id)
          const data = {
            'id': doc.id,
            'name': doc.data().name,
            'type': doc.data().type
          }
          console.log(data)
          this.users.push(data)
        })
      })
    },
    registerNewUser () {
      firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
        .then(user => {
          console.log(`User added ${user.email}`)
        })
        .catch(error => console.log(error))
    },
    addNewUser () {
      db.collection('users').add({
        name: this.name
        // type: this.email
      })
        .then(docRef => console.log(`User added`))
        .catch(error => console.log(error))
    },
    clear () {
      this.$refs.form.reset()
    }
  },
  created () {
    console.log(`Created`)
    /*
    db.collection('users').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc.id)
        const data = {
          'id': doc.id,
          'name': doc.data().name,
          'type': doc.data().type
        }
        console.log(data)
        this.users.push(data)
      })
    }) */
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/scss/index.scss";

* {
  font-family: $text-font;
}

.dashBtn {
  font-weight: 700;
}

</style>



